import { useState } from "react";
import { Main, Form, WrapperLoginPassword, Button } from "./styled";
import "./styles.css";
import { login } from "./utilis.js";

const App = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(null);
  const [isRequesting, setIsRequesting] = useState(false);

  const handleLogin = () => {
    console.log("Enviado");
    setError(null);
    setIsRequesting(true);

    let values = { email: email, password: password };
    login(values)
      .then(() => {
        alert("Seja bem vindo!");
      })
      .catch((error) => {
        console.log(error);
        setError(error.message);
      })
      .finally(() => setIsRequesting(false));
  };

  return (
    <Main>
      <Form>
        <h1>Login</h1>
        {error && <div>{error}</div>}
        <WrapperLoginPassword>
          <input
            placeholder="E-mail"
            type={"email"}
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            label="E-mail"
            required
          ></input>

          <input
            placeholder="Password"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            label="Password"
            required
          ></input>
        </WrapperLoginPassword>
        <Button
          onClick={handleLogin}
          disabled={email === "" || password.length < 6 || isRequesting}
        >
          Entrar
        </Button>
      </Form>
    </Main>
  );
};

export default App;
